	checkTabPermissions( tab ) {
		var retval = false;

		switch ( tab ) {
			case 'tab_analysis':
				retval = Global.getFeatureFlag( 'recruitment_analysis' );
				break;
			default:
				retval = super.checkTabPermissions( tab );
				break;
		}

		return retval;
	}

	setEditViewDataDone() {
		super.setEditViewDataDone();
		if ( this.is_add ) {
			return;
		}

		this.updateEmailLink();

		this.removeFormItemResumePdf();

		// TODO: Remove the old pdfjs framework code, and remove the requirejs calls for it.
		// TODO: Also figure out how we can put the cmap stuff into webpack generated dist folder, so that we dont have to manually update it in framework directorty.

		var $this = this;

		var pdf_form_item_div = $('<div></div>').addClass('edit-view-form-item-div form_item_resume_pdf');
		$this.edit_view_tab.find( '#tab_job_application' ).find( '.first-column' ).append( pdf_form_item_div );

		// API call to get the document URLs
		var args = {};
		args.filter_data = {};
		args.filter_columns = {
			'id': true,
			'document_revision_id': true
		};
		args.filter_data.object_id = this.current_edit_record.job_applicant_id;
		args.filter_data.object_type_id = this.job_applicant_document_object_type_id;
		this.document_api['get' + this.document_api.key_name]( args, {
			onResult: function( resp ) {
				var urls = [];
				var result_data = resp.getResult();
				if ( result_data !== true && _.isArray( result_data ) ) {
					// Once we have the document URL's put them into an Array.
					_.each( result_data, function( row ) {
						var url = ServiceCaller.getURLByObjectType( 'file_download' ) + '&object_type=document&parent_id=' + row.id;
						url = url + '&parent_object_type_id=' + $this.job_applicant_document_object_type_id;
						url = url + '&object_id=' + row.document_revision_id;
						urls.push( url );
					} );

					$this.has_attached_resume = true;
				} else {
					pdf_form_item_div.append('<p class="no-documents-message">' + $.i18n._( 'Sorry, no documents have been attached yet.' ) + '</p>' );
					return;
				}

				// Now that we have retrieved the array of PDF documents, initiate the PDF Viewer.
				new TTPDFViewer({
					urls: urls,
					target: pdf_form_item_div[0]
				});

			}
		} );
	}

