	removeFormItemResumePdf() {
		if ( this.edit_view && this.edit_view.find( '.form_item_resume_pdf' ).length > 0 ) {
			this.edit_view.find( '.form_item_resume_pdf' ).remove();
		}
	}

	onCustomContextClick( id ) {
		switch ( id ) {
			case 'map':
				this.onMapClick();
				break;
			case 'edit_job_applicant':
			case 'edit_employee':
			case 'export_excel':
				this.onNavigationClick( id );
				break;
			case 'analyze':
				this.onAnalyzeClick();
				break;
		}
	}

	onMapClick() {
		// only trigger map load in specific product editions.
		if ( ( Global.getProductEdition() >= 15 ) ) {
			this.is_viewing = false;
			var cells = this.getSelectedItems();
			if ( !this.is_mass_editing ) {
				import( /* webpackChunkName: "leaflet-timetrex" */ '@/framework/leaflet/leaflet-timetrex' ).then(( module )=>{
					var processed_data_for_map = module.TTConvertMapData.processBasicFromGenericViewController( cells );
					IndexViewController.openEditView( this, 'Map', processed_data_for_map );
				}).catch( Global.importErrorHandler );
			}
		}
	}

	onAnalyzeClick() {
		if ( this.has_attached_resume ) {
			this.api['analyzeJobApplication']( this.current_edit_record.id, {
				onResult: ( res ) => {
					let result = res.getResult();

					if ( result ) {
						this.setAnalysis( result );
						TAlertManager.showAlert( $.i18n._( 'Application Analysis Completed' ) );
					} else {
						TAlertManager.showAlert( $.i18n._( 'Failed to Analyze Job Application.' ) );
					}
				}
			} );
		} else {
			TAlertManager.showAlert( $.i18n._( 'Cannot analyze Job Application as no resume is attached.' ) );
		}
	}

	getFilterColumnsFromDisplayColumns() {
		var column_filter = super.getFilterColumnsFromDisplayColumns();
		//Fixes cannot set property job_applicant_id of undefined
		if ( column_filter == undefined ) {
			column_filter = {};
		}
		column_filter.job_applicant_id = true;
		column_filter.latitude = true;
		column_filter.longitude = true;
		return this._getFilterColumnsFromDisplayColumns( column_filter );
	}

	onNavigationClick( iconName ) {
		var $this = this;
		var filter;
		var temp_filter;
		var selected_record;

		switch ( iconName ) {
			case 'edit_job_applicant':
				var current_item = this.getSelectedItem();
				IndexViewController.openEditView( $this, 'JobApplicant', current_item.job_applicant_id );
				break;
			case 'edit_employee':
				filter = { filter_data: {} };
				getUserDefaultData( function( data ) {
					temp_filter = {};
					if ( !$this.edit_view ) {
						selected_record = $this.getSelectedItem();
					} else {
						selected_record = $this.current_edit_record;
					}
					temp_filter.filter_data = {};
					temp_filter.filter_data.id = [selected_record.job_applicant_id];
					$this.job_applicant_api.getJobApplicant( temp_filter, {
						onResult: function( result ) {
							var result_data = result.getResult();
							if ( Global.isArray(result_data) && result_data[0] ) {
								result_data = result_data[0];
								for ( var key in data ) {
									if ( !result_data[key] ) {
										result_data[key] = data[key];
									}
								}
							} else {
								result_data = [];
							}

							IndexViewController.openEditView( $this, 'Employee', result_data );
						}
					} );

				} );

			function getUserDefaultData( callBack ) {
				$this.user_api.getUserDefaultData( {
					onResult: function( result ) {
						var data = result.getResult();
						callBack( data );
					}
				} );
			}

				break;
			case 'export_excel':
				this.onExportClick( 'export' + this.api.key_name );
				break;
		}
	}

	setCurrentEditRecordData() {
		super.setCurrentEditRecordData();
		this.setAnalysis( this.current_edit_record['analysis'] );
	}

	setCustomDefaultMenuIcon( id, context_btn, grid_selected_length ) {
		switch ( id ) {
			case 'edit_job_applicant':
			case 'edit_employee':
				this.setDefaultMenuEditIcon( context_btn, grid_selected_length, 'user' );
				break;
			case 'analyze':
				this.setDefaultMenuAnalyzeIcon( context_btn, grid_selected_length );
				break;
		}
	}

	setCustomEditMenuIcon( id, context_btn ) {
		switch ( id ) {
			case 'analyze':
				this.setEditMenuAnalyzeIcon( context_btn );
				break;
		}
	}

	setDefaultMenuMapIcon( context_btn ) {
		if ( Global.getProductEdition() <= 10 ) {
			ContextMenuManager.hideMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, false )
		}

		var show = false;
		if ( this.grid ) {
			var selected_items = this.getSelectedItems();
			if ( selected_items.length > 0 ) {
				for ( var x = 0; x < selected_items.length; x++ ) {
					if ( selected_items[x] && selected_items[x].latitude && selected_items[x].longitude ) {
						show = true;
						break;
					}
				}
			}
		}

		if ( show ) {
			ContextMenuManager.disableMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, true );
		} else {
			ContextMenuManager.disableMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, false );
		}
	}

	setDefaultMenuAnalyzeIcon( context_btn, grid_selected_length ) {
		//Disabled on list view.
		ContextMenuManager.hideMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, false )
		ContextMenuManager.disableMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, false );
	}

	setEditMenuAnalyzeIcon( context_btn ) {
		if ( Global.getProductEdition() <= 10 || !Global.getFeatureFlag( 'recruitment_analysis' ) ) {
			ContextMenuManager.hideMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, false )
			ContextMenuManager.disableMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, false );
		} else {
			ContextMenuManager.disableMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, true );
			ContextMenuManager.hideMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, true )
		}
	}

	setEditMenuMapIcon( context_btn ) {
		this.setDefaultMenuMapIcon( context_btn );
	}

